* {
  box-sizing: border-box;
  margin: 0;
}

body {
  height: 100vh;
}

#container {
  height: 100%;
  width: 100%;
  background-color: #fcfcfc;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: Verdana, Tahoma, sans-serif;
  display: flex;
}

.site-header {
  height: 30px;
  width: 100%;
  background-color: #444;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 0 10px;
  display: flex;
}

.header-link {
  color: #fcfcfc;
  font-family: Menlo, Consolas, Courier New, Courier, monospace;
  font-size: 16px;
  text-decoration: none;
}

.header-icons {
  gap: 20px;
  display: flex;
}

#dark-mode-toggle {
  cursor: pointer;
  background: none;
  border: none;
}

.github-logo, .moon-icon {
  height: 26px;
  filter: invert();
  padding-top: 4px;
}

.calc {
  height: 600px;
  width: 400px;
  flex-direction: column;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.calc-row {
  flex: 1;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.output {
  width: 100%;
  color: #222;
  background-color: #eaeaea;
  border: 2px solid #d2d2d2;
  border-radius: 25px;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  padding: 0 15px;
  display: flex;
}

.operation {
  min-height: 22.5px;
  font-size: 18px;
}

.result {
  justify-content: flex-end;
  align-items: center;
  font-size: 35px;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #222;
  background-color: #d2d2d2;
  border: none;
  border-radius: 35px;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: inherit;
  font-size: 30px;
  display: flex;
}

.btn:hover {
  background-color: #dfdfdf;
}

.btn:active {
  background-color: #c5c5c5;
}

.btn-xl {
  flex: 2.05;
}

.btn:last-child {
  color: #fff;
  background-color: #f0964b;
}

.btn:last-child:hover {
  background-color: #f2a463;
}

.btn:last-child:active {
  background-color: #e83;
}

@media (max-width: 457px) and (max-aspect-ratio: 2 / 3) {
  #container {
    justify-content: flex-start;
  }

  .site-header {
    margin-bottom: 6.5625vw;
  }

  .calc {
    height: 131.25vw;
    width: 87.5vw;
    gap: 1.64063vw;
  }

  .calc-row {
    gap: 1.64063vw;
  }

  .output {
    border-radius: 5.46875vw;
    padding: 0 3.28125vw;
  }

  .operation {
    min-height: 4.92188vw;
    font-size: 3.9375vw;
  }

  .result {
    font-size: 7.65625vw;
  }

  .btn {
    border-radius: 7.65625vw;
    font-size: 6.5625vw;
  }
}

@media (max-height: 690px) and (min-aspect-ratio: 2 / 3) {
  #container {
    justify-content: flex-start;
  }

  .site-header {
    margin-bottom: 4.25vh;
  }

  .calc {
    height: 85vh;
    width: 56.6667vh;
    gap: 1.0625vh;
  }

  .calc-row {
    gap: 1.0625vh;
  }

  .output {
    border-radius: 3.54167vh;
    padding: 0 2.125vh;
  }

  .operation {
    min-height: 3.1875vh;
    font-size: 2.55vh;
  }

  .result {
    font-size: 4.95833vh;
  }

  .btn {
    border-radius: 4.95833vh;
    font-size: 4.25vh;
  }
}

#container.dark-mode {
  background-color: #262626;
}

#container.dark-mode .output {
  color: #fcfcfc;
  background-color: #2f2f2f;
  border-color: #3b3b3b;
}

#container.dark-mode .btn {
  color: #fcfcfc;
  background-color: #3b3b3b;
}

#container.dark-mode .btn:hover {
  background-color: #484848;
}

#container.dark-mode .btn:active {
  background-color: #2e2e2e;
}

#container.dark-mode .btn:last-child {
  background-color: #ad570e;
}

#container.dark-mode .btn:last-child:hover {
  background-color: #c56210;
}

#container.dark-mode .btn:last-child:active {
  background-color: #964b0c;
}

